@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,900);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body {
    font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    font-weight: 300;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    display: -webkit-flex;
    display: flex;
    min-height: 100vh;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0;
    font-size: 16px;
}

#root{
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.roboto-light {
    font-family: "Roboto", sans-serif;
    font-weight: 300;
}

.roboto-regular {
    font-family: "Roboto", sans-serif;
    font-weight: 400;
}

.roboto-black {
    font-family: "Roboto", sans-serif;
    font-weight: 900;
}

.color-red {
    color: #D42026;
}

.background-red {
    background-color: #D42026;
}

.color-black {
    color: #4e4e4e;
}

.color-white {
    color: #ffffff;
}


.container {
    margin: 20px 42px 0 42px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
}

.header-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 100%;
    margin-bottom: 34px;
}

 .title {
     font-size: 27px;
     line-height: 1.2;
 }

 .title__bar {
     height: 4px;
     width: 54px;
 }

 .page-title {
     display: -webkit-flex;
     display: flex;
     -webkit-flex-direction: row;
             flex-direction: row;
 }


.footer {
    width: 100vw;
    height: 12px;
    position: fixed;
    left: 0;
    bottom: 0;
}

.search {
    margin-bottom: 34px;
}

.search__label {
    display: inline-block;
    font-size: 16px;
    line-height: 1.2;
    margin-bottom: .2rem;
}

.search__input {
    display: block;
    font-family: inherit;
    padding-left:  14px;
    width: 384px;
    height: 29px;
    font-size: 1rem;
    line-height: 1.2;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #A5A5A5;
    border-radius: .25rem;
    -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    overflow: visible;
}

.search__input:focus {
    outline: none;
    /*border-color: #D42026;*/
    /*box-shadow: 0 0 5px #D42026;*/
}

.table {
    border-collapse: collapse;
    width: 100%;
}

.table tr {
    display: table-row;
    border-color: inherit;
}

.table th {
    border-right: 10px solid #fff;
}
.table th:last-child {
    border-right: none;
}

.table th {
    font-size: 16px;
    line-height: 1.2;
    padding: 10px 0 10px 14px;
    text-align: left;
    width: 25%;
}

.table td{
    padding: 20px 0 20px 14px;
    border-bottom: 3px solid #f6d2d3;
    font-size: 21px;
    line-height: 1.1;
}

.table td:last-child, th:last-child {
    width: 50%;
}

.table tbody tr:hover {
    cursor: pointer;
   background-color: #fbe9e9;
}

.hero-card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

.hero-card__image {
    margin-right: 25px;
    width: 58px;
    height: 58px;
    border-radius: 300px;
}

.hero-card__name {
    font-size: 21px;
    line-height: 1.1;
}


.paginator {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.page-number {
    width: 32px;
    height: 32px;
    border: 1px solid #d42026;
    border-radius: 300px;
    font-size: 21px;
    line-height: 1.1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-right: 20px;
    cursor: pointer;
}

.paginator div:nth-last-child(2) {
    margin-right: 0;
}

.search-page {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;
}

